import React,{useEffect} from 'react';
import {useState} from 'react';
import {ColorPicker, useColor} from "react-color-palette";
import '../css/Modal.css';

const DetailModal = (props) => {

    const { open, close, add, header } = props;

    const[changeNum, setChangeNum] = useState(1);
    const[editTitle, setEditTitle] = useState('');
    const[startDate, setStartDate] = useState('');
    const[endDate, setEndDate] = useState('');
    const[color, setColor] = useColor("hex", "#121212");

    const changeStart = (e) => {
        setStartDate(e.target.value);
    }

    const changeEnd = (e) => {
        setEndDate(e.target.value);
    }

    const changeTitle = (e) => {
        setEditTitle(e.target.value);
    }

    //     
    const editEvent = (e) => {
        console.log('  ');

        setChangeNum(changeNum+1); // title    (    ) title  
        props.propFunction(editTitle, startDate, endDate, color.hex); // editTitle  ( title)
    }

    //     
    const deleteEvent = (e) => {
        console.log('Event ');

        props.propFunction2(deleteEvent);
    }

    useEffect(()=> {
        setEditTitle('');
        setStartDate('');
        setEndDate('');
    }, [changeNum])

    return (
        <div className={open ? 'openModal modal' : 'modal'}>
                  {open ? (
                    <section>
                      <header>
                        {header}
                        <button className="close" onClick={close}>
                          &times;
                        </button>
                      </header>

                    <div className="modal-body">
                        <div className="form-group">
                            <label htmlFor="taskId" className="col-form-label"> </label>
                            <input type="text" onChange={changeTitle} className="form-control" value={editTitle} id="calendar_edit_title" name="calendar_edit_title"/>
                            <label htmlFor="taskId" className="col-form-label"> </label>
                            <input type="datetime-local" onChange={changeStart} className="form-control" value={startDate} id="calendar_start_date" name="calendar_start_date"/>
                            <label htmlFor="taskId" className="col-form-label"> </label>
                            <input type="datetime-local" onChange={changeEnd} className="form-control" value={endDate} id="calendar_end_date" name="calendar_end_date"/>
                            <br />
                            <ColorPicker width={400} height={100} color={color} onChange={setColor} hideHSV dark />
                        </div>
                    </div>

                    <footer>
                        <button className="edit" onClick={editEvent}>
                          
                        </button>
                        <button className="delete" onClick={deleteEvent}>
                          
                        </button>
                    </footer>

                    </section>
                  ) : null}
                </div>
    );
};

export default DetailModal;